<template>
  <div>
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
        <ul>
            <li v-for="(item,index) in goods" :key="index" class="menu-item" :class="{'current': currentIndex===index}" @click="selectMenu(index)">
                <span class="text">
                    <span v-show="item.type > 0" class="icon" :class="classMap[item.type]"></span>
                    {{item.name}}
                </span>
            </li>
        </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
        <ul>
            <li v-for="(item, index) in goods" :key="index" class="food-list" ref="foodList">
                <h1 class="title"> {{item.name}} </h1>
                <ul>
                    <li v-for="(food, index) in item.foods" :key="index" class="food-item" @click="selectFood(food, $event)">
                        <div class="icon">
                            <img width="57px" height="57px" :src=food.icon alt="">
                        </div>
                        <div class="content">
                            <h2 class="name">
                                {{food.name}}
                            </h2>
                            <p class="desc">
                                {{food.description}}
                            </p>
                            <div class="extra">
                                <span class="count">
                                    月售{{food.sellCount}}
                                </span><span>好评率{{food.rating}}</span>
                            </div>
                            <div class="price">
                                <span class="now">${{food.price}}</span>
                                <span class="old" v-show="food.oldPrice">${{food.oldPrice}}</span>
                            </div>
                            <div class="cartcontrol-wrapper">
                                <CartControl @add="addFood" :food="food" />
                            </div>
                        </div>
                    </li>
                </ul>
            </li>
        </ul>
    </div>
    <ShopCart ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice" />
  </div>
  <Food @add="addFood" :food="selectedFood" ref="food"/>
  </div>
</template>

<script>
import axios from 'axios'
import BScroll from 'better-scroll'
import ShopCart from 'components/shopCart/ShopCart.vue'
import Food from 'components/food/Food.vue'
import CartControl from 'components/cartControl/CartControl.vue'

export default {
    components: {
        ShopCart,
        CartControl,
        Food
    },
    data() {
        return {
            goods: [],
            listHeight: [],
            scrollY: 0,
            selectedFood: {}
        }
    },
    props: {
        seller: {
            default: {},
            type:Object
        }
    },
    created() {
        this.classMap = ["decrease", "discount", "special", "invoice", "guarantee"];
    },
    mounted() {
        this.$http.get('../../data.json').then(res => {
            if(res.status === 200) {
                this.goods = res.data.goods
                this.$nextTick(() => {
                    this._initScroll()
                    this._calculateHeight()
                })
            }
        })
    },
    computed: {
        selectFoods() {
            let foods = []
            this.goods.forEach(good => {
                good.foods.forEach(food => {
                    if(food.count) {
                        foods.push(food)
                    }
                })
            })
            return foods
        },
        currentIndex() {
            for (let i = 0; i < this.listHeight.length; i++) {
                let height1 = this.listHeight[i];
                let height2 = this.listHeight[i + 1];
                if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
                    // this._followScroll(i);
                    return i;
                }
            }
            return 0;   
      },
        
    },
    events: {
        'cart.add'(target) {
            this._drop(target)
        }
    },
    methods: {
        addFood(target) {
            this._drop(target);
        },
        _drop(target) {
            console.log(this.$refs)
            this.$refs.shopcart.drop(target)
        },
        selectMenu(index){
            this.foodsScroll.scrollTo(0,-this.listHeight[index],300)
        },
        _initScroll() {
        this.meunScroll = new BScroll(this.$refs.menuWrapper, {
          click: true
        });

        this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
          click: true,
          probeType: 3
        });

        this.foodsScroll.on('scroll', (pos) => {
          // 判断滑动方向，避免下拉时分类高亮错误（如第一分类商品数量为1时，下拉使得第二分类高亮）
          if (pos.y <= 0) {
            this.scrollY = Math.abs(Math.round(pos.y));
          }
        });
      },
      _calculateHeight() {
        let foodList = this.$refs.foodList;
        let height = 0;
        this.listHeight.push(height);
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i];
          height += item.clientHeight;
          this.listHeight.push(height);
        }
      },
      selectFood(food, event) {
          this.selectedFood = food
          this.$refs.food.show()
      }
    }
};
</script>

<style lang="stylus" scoped>
@import '../../common/stylus/mixin'; 
    .goods
        display flex
        position absolute
        top 174px
        bottom 46px
        width 100%
        overflow hidden
        .menu-wrapper
            flex 0 0 80px
            width  80px
            background-color #f3f5f7
            .menu-item 
                display table
                width 56px
                height 54px
                line-height 14px
                padding 0 12px
                &.current
                    position relative
                    margin-top -1px
                    z-index 10
                    background-color #fff
                    font-weight 700
                    .text
                        border none
                        font-weight 700
                .text 
                    display table-cell
                    width 56px
                    vertical-align middle
                    border-bottom  1px solid rgba(7, 17, 27, .1)
                    font-size 12px
                .icon
                    display: inline-block;
                    vertical-align: middle;
                    width: 12px;
                    height: 12px;
                    margin-right: 2px;
                    background-size: cover;
                    &.decrease 
                        bg-image('./decrease_3');
                    &.discount 
                        bg-image('./discount_3');
                    &.guarantee 
                        bg-image('./guarantee_3');
                    &.special 
                        bg-image('./special_3');
                    &.invoice 
                        bg-image('./invoice_3');
        .foods-wrapper
            flex 1
            .title
                padding-left 14px
                height 26px
                line-height 26px
                border-left 2px solid #d9dde1
                font-size 12px
                color rgb(147, 153, 159)
                background-color #f3f5f7
            .food-item 
                display flex
                margin 18px
                padding-bottom 18px
                border-bottom 1px solid #d9dde1
                &:last-child 
                    border-bottom none
                    margin-bottom 0
                .icon
                    flex 0 0 57px
                    margin-right 10px
                .content
                    position relative
                    flex 1
                    .name
                        margin 2px 0 8px 0
                        height 14px
                        line-height 14px
                        font-size 14px
                        color rgb(7,17,27)
                    .desc, .extra
                        line-height 10px
                        font-size 10px
                        color rgb(147, 153, 159)
                    .desc 
                        margin-bottom 8px
                        line-height 12px
                    .extra
                        .count 
                            margin-right 12px
                    .price
                        font-weight 700
                        line-height 24px
                        .now 
                            margin-right 8px
                            color rgb(240, 20, 20)
                            font-size: 14px
                        .old
                            text-decoration line-through
                            font-size: 10px
                            color: rgb(147, 153, 159)
                    .cartcontrol-wrapper
                        position absolute
                        right 0 
                        bottom 12px
        



         
</style>
