<template>
  <div class="ratingselect">
    <div class="rating-type">
      <span @click="select(2, $event)" class="block positive" :class="{'active': selectType===2}">
          {{desc.all}}
          <span class="count">{{ratings.length}}</span>
      </span>
      <span @click="select(0, $event)" class="block positive" :class="{'active': selectType===0}">
          {{desc.positive}}
          <span class="count">{{positive.length}}</span>
      </span>
      <span @click="select(1, $event)" class="block negative" :class="{'active': selectType===1}">
          {{desc.negative}}
          <span class="count">{{ratings.length - positive.length}}</span>
      </span>
    </div>
    <div @click="switchContent" class="switch" :class="{'on': onlyContent}">
        <i class="icon-check_circle"></i>
        <span class="text">
            只看有内容的评价
        </span>
    </div>
  </div>
</template>

<script>
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;
export default {
  props: {
    ratings: {
      type: Array,
      default() {
        return [];
      }
    },
    selectType: {
      type: Number,
      default: ALL
    },
    onlyContent: {
      type: Boolean,
      default: false
    },
    desc: {
      type: Object,
      default() {
        return {
          all: "全部",
          positive: "满意",
          negative: "不满意"
        };
      }
    }
  },
  computed: {
      positive() {
          return this.ratings.filter(rating => {
              return rating.rateType === POSITIVE 
          })
      }
  },
  methods: {
      select(type, event) {
          
        //   this.selectType = type
          this.$emit('ratingTypeChange', type)
      },
      switchContent() {
        //   this.onlyContent = !this.onlyContent
          this.$emit('contentToggle', this.onlyContent)
      }
  }
};

</script>

<style lang="stylus" scoped>
.ratingselect
    .switch
        font-size 0
        padding 12px 18px
        line-height 24px
        border-bottom 1px solid rgba(7,17,27,.1)
        color rgb(147, 153, 159)
        .icon-check_circle
            display inline-block
            vertical-align top 
            font-size 24px
        &.on
            .icon-check_circle
                color #00c850
        .text
            display inline-block
            vertical-align top 
            font-size 12px
    .rating-type
        padding 18px 0
        margin 0 18px
        border-bottom 1px solid rgba(7,17,27,.1)
        font-size 0
        .block
            display inline-block
            padding 8px 12px
            margin-right 8px
            border-radius 1px
            line-height 16px
            font-size 12px
            color rgb(77, 85, 93)
            &.active
                color #ffffff
            .count 
                font-size 8px
                margin-left 2px
        .positive
            color rgb(77, 85, 93)
            background-color rgba(0, 160, 220, 0.2)
            &.active
                background-color rgb(0, 160, 220)
        .negative
            background-color rgba(77, 85, 93, 0.2)        
            color rgb(77, 85, 93)
            &.active
                background-color rgb(77, 85, 93)   
</style>
