<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img :src="seller.avatar" height="64" :alt="seller.name+'.jpg'">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">{{seller.description}} / {{seller.deliveryTime}} 送达</div>
        <div class="support" v-if="seller.supports">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div v-if="seller.supports" class="support-count" @click="showDetail">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetail">
      <span class="bulletin-title"></span>
      <span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" :alt="seller.name" width="100%" height="100%">
    </div>
    <transition name="fade">
        <div v-show="detailShow" class="detail">
            <div class="detail-wrapper clearfix">
                <div class="detail-main">
                    <h1 class="name">
                        {{seller.name}}
                    </h1>
                    <div class="star-wrapper">
                        <Star :score="seller.score" :size="48" />
                    </div>
                    <div class="title">
                        <div class="line"></div>
                        <div class="text">优惠信息</div>
                        <div class="line"></div>
                    </div>
                    <ul class="supports" v-if="seller.supports">
                        <li class="support-item" v-for="(item, index) in seller.supports" :key="index">
                            <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                            <span class="text">
                                {{ seller.supports[index].description }}
                            </span>
                        </li>
                    </ul>
                    <div class="title">
                        <div class="line"></div>
                        <div class="text">商家公告</div>
                        <div class="line"></div>
                    </div>
                    <div class="bulletin">
                        <p class="content">
                            {{seller.bulletin}}
                        </p>
                    </div>
                </div>
            </div>
        <div class="detail-close" @click="hideDetail">
            <i class="icon-close"></i>
        </div>
    </div>
    </transition>
    
  </div>
</template>

<script>
import Star from 'components/star/Star.vue'
export default {
  name: "Header",
  components: {
      Star
  },
  data() {
    return {
      detailShow: false
    };
  },
  props: {
    seller: {
      type: Object,
      default: {}
    }
  },
  methods: {
    showDetail() {
        this.detailShow = true
    },
    hideDetail() {
        this.detailShow = false
    }
  },
  created() {
    this.classMap = ["decrease", "discount", "special", "invoice", "guarantee"];
  }
};
</script>


<style lang="stylus" scoped>
@import '../../common/stylus/mixin';

.header {
  position: relative;
  overflow: hidden;
  background-color: rgba(7, 17, 27, 0.5);
  color: #ffffff;

  .content-wrapper {
    padding: 24px 12px 18px 24px;
    font-size: 0;
    position: relative;

    .avatar {
      display: inline-block;
      vertical-align: top;

      img {
        border-radius: 2px;
      }
    }

    .support-count {
      position: absolute;
      right: 12px;
      bottom: 14px;
      padding: 0 8px;
      height: 24px;
      line-height: 24px;
      border-radius: 14px;
      background-color: rgba(0, 0, 0, 0.2);
      text-align: center;

      .count {
        vertical-align: top;
        font-size: 10px;
      }

      .icon-keyboard_arrow_right {
        margin-left: 2px;
        line-height: 24px;
        font-size: 10px;
      }
    }

    .content {
      display: inline-block;
      margin-left: 16px;
      font-size: 14px;

      .description {
        margin-bottom: 10px;
        line-height: 12px;
        font-size: 12px;
      }

      .title {
        margin: 2px 0 8px 0;

        .brand {
          display: inline-block;
          vertical-align: top;
          width: 30px;
          height: 18px;
          bg-image('./brand');
          background-size: cover;
        }

        .name {
          margin-left: 6px;
          font-size: 16px;
          line-height: 18px;
          font-weight: bold;
        }
      }

      .support {
        .text {
          font-size: 10px;
        }

        .icon {
          display: inline-block;
          vertical-align: middle;
          width: 12px;
          height: 12px;
          margin-right: 4px;
          background-size: cover;

          &.decrease {
            bg-image('./decrease_1');
          }

          &.discount {
            bg-image('./discount_1');
          }

          &.guarantee {
            bg-image('./guarantee_1');
          }

          &.special {
            bg-image('./special_1');
          }
          &.invoice {
            bg-image('./invoice_1');
          }
        }
      }
    }
  }

  .bulletin-wrapper {
    position: relative;
    height: 28px;
    line-height: 28px;
    padding: 0 22px 0 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background-color: rgba(7, 17, 27, 0.2);

    .bulletin-title {
      display: inline-block;
      vertical-align: top;
      margin-top: 8px;
      width: 22px;
      height: 12px;
      bg-image('bulletin');
      background-size: cover;
    }

    .bulletin-text {
      vertical-align: top;
      margin: 0 4px;
      font-size: 10px;
    }

    .icon-keyboard_arrow_right {
      position: absolute;
      font-size: 10px;
      right: 12px;
      top: 8px;
    }
  }

  .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    filter: blur(10px);
  }

  .detail {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 51;
    width: 100%;
    height: 100%;
    overflow: auto;
    transition all .3s ease-in-out
    backdrop-filter blur(10px)
    background-color: rgba(7, 17, 27, 0.8);
    &.fade-transition {
        opacity 1
    }
    &.fade-enter, &.fade-leave {
        opacity 0
    }
    .detail-wrapper {
        width 100%
        min-height 100%;
        .detail-main {
            margin-top 64px
            padding-bottom 64px    
            .name {
                line-height 16px   
                text-align center
                font-size  16px
                font-weight 700 
            }
            .star-wrapper {
                margin-top 18px
                padding 2px 0 
                text-align center    
            }
            .title {
                display flex
                width 80%
                margin 28px auto 24px
                .line {
                    flex 1
                    position relative
                    top -6px
                    border-bottom 1px solid rgba(255,255,255, .2)
                }
                .text {
                    padding 0 12px;
                    font-size 14px  
                    font-weight 700 
                }
            }
            .supports {
                width 80%
                margin 0 auto 
                .support-item {
                    padding 0 12px  
                    margin-bottom 12px
                    font-size 0  
                    &:last-child {
                        margin-bottom 0
                    }
                    .icon {
                        display inline-block
                        width  16px
                        height 16px
                        vertical-align  top
                        margin-right 6px
                        background-size cover
                        &.decrease {
                            bg-image('./decrease_2');
                        }

                        &.discount {
                            bg-image('./discount_2');
                        }

                        &.guarantee {
                            bg-image('./guarantee_2');
                        }

                        &.special {
                            bg-image('./special_2');
                        }
                        &.invoice {
                            bg-image('./invoice_2');
                        }
                    }
                    .text {
                        line-height 12px
                        font-size 12px
                    }
                }
            }
            .bulletin {
                width:80%
                margin: 0 auto 
                .content {
                    padding: 0 12px
                    line-height 24px
                    font-size 12px
                }
            }
        }    
    }
    .detail-close {
        position relative
        width 32px
        height 32px 
        margin -64px auto 0 auto
        clear both
        font-size  32px
    }
  }
}
</style>
